/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
body {
  font-family: PingFangSC-Regular;
  font-size: 32px;
}
a:link,a:visited{
  text-decoration:none;  /*超链接无下划线*/
 }
 a:hover{
  text-decoration:underline;  /*鼠标放上去有下划线*/
 }


