@charset "UTF-8";
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html,
body {
  font-family: PingFangSC-Regular;
  font-size: 32px; }

a:link, a:visited {
  text-decoration: none;
  /*超链接无下划线*/ }

a:hover {
  text-decoration: underline;
  /*鼠标放上去有下划线*/ }

.demo-preview-item .am-slider-wrapper {
  margin-bottom: 15px; }

.demo-preview-item .am-slider-example {
  overflow: hidden; }

.am-wingblank.am-wingblank-lg {
  margin-bottom: 30px; }

.demo-preview-item .am-slider-wrapper:last-child {
  margin-bottom: 10px; }

.sub-title {
  color: #888;
  font-size: 14px;
  padding: 30px 0 18px 0;
  margin: 0; }

.homeContainer {
  width: 100%;
  height: 100%;
  background: url("../../static/images/home_bg@2x.png") no-repeat center;
  background-size: 100% 100%; }
  .homeContainer .homeReading {
    width: 100%;
    display: flex;
    justify-content: center; }
    .homeContainer .homeReading li {
      width: 3.875rem;
      height: 1.15rem;
      margin-top: 12.85rem;
      border-radius: 0.6625rem;
      opacity: 0.6;
      background-image: linear-gradient(-180deg, #c5ba9c 0%, #c1b696 100%);
      box-shadow: 0 10px 14px 0;
      font-size: 0.45rem;
      line-height: 1.15rem;
      font-family: PingFangSC-Regular;
      color: #ffffff;
      letter-spacing: 0;
      text-align: center; }
      .homeContainer .homeReading li.continue {
        margin-left: 0.375rem; }
