@charset "UTF-8";
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html,
body {
  font-family: PingFangSC-Regular;
  font-size: 32px; }

a:link, a:visited {
  text-decoration: none;
  /*超链接无下划线*/ }

a:hover {
  text-decoration: underline;
  /*鼠标放上去有下划线*/ }

.demo-preview-item .am-slider-wrapper {
  margin-bottom: 15px; }

.demo-preview-item .am-slider-example {
  overflow: hidden; }

.am-wingblank.am-wingblank-lg {
  margin-bottom: 30px; }

.demo-preview-item .am-slider-wrapper:last-child {
  margin-bottom: 10px; }

.sub-title {
  color: #888;
  font-size: 14px;
  padding: 30px 0 18px 0;
  margin: 0; }

.header {
  width: 100%;
  height: 1.6rem;
  background: #c1b696;
  display: flex;
  align-items: center;
  font-size: 0.45rem;
  color: #fff; }
  .header .note {
    margin-left: 32%; }

.content {
  display: flex;
  flex-direction: column; }
  .content li {
    display: flex;
    height: 1.15rem;
    align-items: center;
    border-bottom: 1px solid #e2e2da;
    justify-content: space-between;
    padding: 0 20px;
    font-size: 0.3rem;
    color: #474747;
    letter-spacing: 0; }
    .content li span:nth-of-type(1) {
      background: #e0d0bb;
      width: 0.325rem;
      height: 0.325rem;
      border-radius: 100%; }
    .content li span:nth-of-type(2) {
      margin-left: -1.25rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap; }

.new {
  background: #c1b696;
  box-shadow: 0 4px 7px 0 rgba(193, 182, 150, 0.6);
  width: 44px;
  height: 44px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 40%;
  left: 45%; }
  .new .icon {
    width: 26px;
    height: 26px;
    background: url("../../../static/images/pen.png") no-repeat;
    background-size: 26px 26px; }
